.wrap_teacher {
  position: relative;
  display: flex;
  justify-content: space-between;
  gap: 48px;
  max-width: 1184px;
  margin: 0 auto;
  border-radius: 24px;
  background: #fff;
  padding: 24px;
  margin-bottom: 32px;
}

.teacher_info {
  width: 80%;
}

.favorit_position {
  display: none;
}

@media (max-width: 319px) {
  .wrap_teacher {
    flex-direction: column;
    align-items: center;
    max-width: 319px;
  }
}

@media (min-width: 320px) and (max-width: 700px) {
  .wrap_teacher {
    flex-direction: column;
    align-items: center;
  }
}

@media (max-width: 1000px) {
  .favorit_position {
    position: absolute;
    display: block;
    top: 24px;
    right: 24px;
  }
}
